// Breadcrumb item arrow
.breadcrumb-item {
     > a {
        color: var(--#{$prefix}gray-700);
    }
    +.breadcrumb-item {
        &::before {
            font-family: "Material Design Icons";
            font-size: 16px;
            line-height: 1.3;
        }
    }
}